/* Question Panel */
.question-panel {
  @extend .u-base-panel;

  &__heading { @extend .u-base-panel__heading; }
  &__content { @extend .u-base-panel__content; }
  
  &__action { 
    @extend .u-base-panel__action; 
    position: absolute;
    bottom: 0;
  }
  &__action__title { @extend .u-base-panel__action__title; }
  &__action__content { @extend .u-base-panel__action__content; }
  

  &__form {
    input,
    textarea,
    .select { margin-bottom: 25px; }

    textarea { 
      height: 240px; 
      min-height: 240px; 
      max-height: 320px; 
    }
  }

  &__error {}
  &--success {}
  &--success__heading {}
  &--success__icon {}
}